<svelte:options tag="pl-inquiry" />

<script>
    import { onMount } from "svelte"
    import { createMachine, interpret } from "xstate"
    import { http } from "../utils/http"
    import { scale } from "svelte/transition"

    $: inquiry = false
    $: data = {
        name: null,
        salutation: "Herr",
        company: null,
        street: null,
        zip: null,
        city: null,
        email: null,
        phone: null,
        type: "",
        date: null,
        locations: [],
        amount_guests: "",
        catering_needed: "",
        technic_needed: "",
        text: null,
    }
    $: collapse = (key) => {
        let result = false

        switch (key) {
            case "type":
                result = true
                break
            case "amount_guests":
                result = data.type
                break
            case "date":
                result = data.type && data.amount_guests
                break
            case "locations":
                result = data.type && data.amount_guests && data.date
                break
            case "catering_needed":
                result =
                    data.type &&
                    data.amount_guests &&
                    data.date &&
                    data.locations.length
                break
            case "technic_needed":
                result =
                    data.type &&
                    data.amount_guests &&
                    data.date &&
                    data.locations.length &&
                    data.catering_needed
                break
            case "contact":
                result =
                    data.type &&
                    data.amount_guests &&
                    data.date &&
                    data.locations.length
                break
        }

        return result
    }

    let apiState = false
    let formState = false

    const apiMachine = createMachine({
        id: "api",
        initial: "inactive",
        states: {
            inactive: {
                on: {
                    SUBMIT: { target: "loading" },
                },
            },
            loading: {
                on: {
                    RESOLVE: { target: "success" },
                    REJECT: { target: "error" },
                },
            },
            success: {},
            error: {
                on: {
                    SUBMIT: { target: "loading" },
                },
            },
        },
    })

    const apiService = interpret(apiMachine)
        .onTransition((state) => (apiState = state))
        .start()

    const formMachine = createMachine({
        id: "form",
        initial: "new",
        states: {
            new: {
                on: {
                    NEXT: { target: "finished" },
                    SKIP: { target: "finished" },
                },
            },
            finished: {
                on: {
                    RESET: { target: "new" },
                },
            },
        },
    })

    const formService = interpret(formMachine)
        .onTransition((state) => (formState = state))
        .start()

    onMount(() => {
        const storedInquiry = JSON.parse(localStorage.getItem("event-inquiry"))

        if (storedInquiry) {
            let createdAt = new Date(storedInquiry.created_at)
            createdAt = createdAt.toISOString().split("T")[0]
            let threshold = new Date()
            threshold.setTime(threshold.getTime() - 2629800000)
            threshold = threshold.toISOString().split("T")[0]
            if (threshold > createdAt) {
                localStorage.removeItem("event-inquiry")
            } else {
                inquiry = storedInquiry
                formService.send("SKIP")
            }
        }
    })

    async function submit() {
        try {
            apiService.send("SUBMIT")
            let temp = { ...data }

            temp.catering_needed =
                data.catering_needed === "true" ? true : false
            temp.technic_needed = data.technic_needed === "true" ? true : false
            if (!temp.company) delete temp.company
            if (!temp.text) delete temp.text

            const response = await http
                .post("event-inquiries/submit", {
                    json: temp,
                })
                .json()

            localStorage.removeItem("autosave")
            localStorage.setItem("event-inquiry", JSON.stringify(response))
            inquiry = response
            apiService.send("RESOLVE")
            formService.send("NEXT")
        } catch (err) {
            console.log(err)
            apiService.send("REJECT")
        }
    }

    function reset() {
        data = {
            name: null,
            salutation: "Herr",
            company: null,
            street: null,
            zip: null,
            city: null,
            email: null,
            phone: null,
            type: "",
            date: null,
            locations: [],
            amount_guests: "",
            catering_needed: "",
            technic_needed: "",
            text: null,
            website: null,
        }
        formService.send("RESET")
    }

    function locationSelect() {
        if (this.checked) {
            data.locations = [...data.locations, Number(this.dataset.id)]
        } else {
            data.locations = data.locations.filter(
                (location) => location !== Number(this.dataset.id)
            )
        }
    }
</script>

<link
    href="https://fonts.googleapis.com/css?family=Montserrat"
    rel="stylesheet"
/>
<link
    href="https://cdn.jsdelivr.net/npm/bootstrap@5.1.3/dist/css/bootstrap.min.css"
