                    <div class="attribute">
                        <div class="label"><b>Anmerkungen:</b></div>
                        <div class="value">{inquiry.text}</div>
                    </div>
                {/if}
            </div>
            <div style="text-align: center; margin-top: 3em">
                <button type="button" class="btn btn-primary" on:click={reset}>
                    Neue Anfrage stellen
                </button>
            </div>
        </div>
    </main>
