<svelte:options tag="svelte-counter" />

<script>
    $: counter = 0

    function handleClick() {
        counter += 1
    }
</script>

<main>
    <button on:click={handleClick}>Klick</button>
    {counter}
</main>

<style>
    main {
        text-align: center;
        padding: 1em;
        max-width: 240px;
        margin: 0 auto;
    }

    @media (min-width: 640px) {
        main {
            max-width: none;
        }
    }
</style>
