import ky from "ky"

const prefixUrl =
    process.env.NODE_ENV === "production"
        ? process.env.API_URL
        : process.env.API_URL_DEV

let options = {
    prefixUrl: prefixUrl,
    credentials: "include",
    headers: {},
    hooks: {},
}

const http = ky.extend(options)

export { http, prefixUrl }
